@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,400;0,700;1,500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Shrikhand&display=swap");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video, button {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  min-height: 0vw;
  text-decoration: none;
  color: inherit;
  background-color: transparent;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  line-height: 1.4rem;
  box-sizing: border-box;
  background-color: bisque;
  width: 100%;
  color: #000;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 100;
  font-size: 1rem;
}
@media (min-width: 768px) {
  body {
    font-family: "Roboto", sans-serif;
    font-weight: 100;
    font-size: 1.1rem;
  }
}

h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 1.5rem;
}
h1.title__restaurant {
  padding: 2rem 1rem 0 1rem;
  font-family: "Shrikhand", cursive;
}
@media (min-width: 1200px) {
  h1.title__restaurant {
    text-align: center;
    padding: 3rem 0 2rem 0;
  }
}

h2 {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 1.4rem;
}
h2.menu-title {
  position: relative;
  font-family: "Roboto", sans-serif;
  font-weight: 100;
  font-size: 0.8rem;
  text-transform: uppercase;
  padding-bottom: 0.1rem;
}
h2.menu-title:after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 1px;
  width: 10%;
  border-bottom: 2px solid #99E2D0;
}

h3 {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 1.1rem;
}
h3.dish-title {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 0.9rem;
}
@media (min-width: 576px) {
  h3.dish-title {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: 1rem;
  }
}

.dish-description {
  font-family: "Roboto", sans-serif;
  font-weight: 100;
  font-size: 0.8rem;
}
@media (min-width: 576px) {
  .dish-description {
    font-family: "Roboto", sans-serif;
    font-weight: 100;
    font-size: 0.9rem;
  }
}
@media (min-width: 768px) {
  .dish-description {
    font-family: "Roboto", sans-serif;
    font-weight: 100;
    font-size: 1rem;
  }
}

.dish-price {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 0.8rem;
}
@media (min-width: 576px) {
  .dish-price {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: 0.9rem;
  }
}
@media (min-width: 768px) {
  .dish-price {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: 1rem;
  }
}

.header {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.header__figure {
  flex-basis: 100%;
  background-color: #FFF;
  text-align: center;
  padding: 1.3rem 0 0.8rem 0;
}
.header__form {
  flex-basis: 100%;
  text-align: center;
  padding: 1.2rem 0 1rem 0;
}

.header-nav {
  background-color: #FFF;
}
.header-nav__bar {
  position: relative;
  height: 55px;
  text-align: center;
}
.header-nav__bar .nav-logo img {
  margin-top: 1rem;
  height: 25px;
}
.header-nav__bar .btn-return {
  position: absolute;
  top: 0;
  left: 0;
  width: 70px;
  height: 60px;
}
.header-nav__bar .btn-return i {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.header__form {
  background-color: #EAEAEA;
  box-shadow: inset 1px 4px 9px -8px;
}
.header__form .form__input {
  background-color: #EAEAEA;
  border: 1px solid transparent;
  padding-left: 0.5rem;
  width: 125px;
}
.header__form .form__input::placeholder {
  color: #000;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1rem;
}

.section-cta {
  background-color: #F6F6F6;
  padding: 2.5rem 1.5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .section-cta__title {
    font-size: 1.6rem;
    padding: 1rem 2.5rem 0 2.5rem;
  }
}
.section-cta__description {
  margin: 1rem 0 1.5rem 0;
}
@media (min-width: 768px) {
  .section-cta__description {
    font-size: 1.2rem;
    margin: 1rem 0 2rem 0;
  }
}

.section-tutorial {
  background-color: #FFF;
  padding: 2.5rem 1.5rem;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  gap: 0 2rem;
}
.section-tutorial__card {
  flex-basis: 100%;
}
@media (min-width: 768px) {
  .section-tutorial__card {
    align-content: center;
  }
  .section-tutorial__card:nth-child(1) {
    text-align: center;
    flex-basis: 100%;
  }
  .section-tutorial__card:nth-child(n+2) {
    min-width: 300px;
    flex-basis: 30%;
  }
}
.section-tutorial__card:nth-child(1) {
  margin-bottom: 1.5rem;
}

.section-restaurants {
  background-color: #F6F6F6;
  padding: 2.5rem 1.5rem;
}
.section-restaurants__list {
  padding: 1.5rem 0 0 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
.section-restaurants__list .restaurant-card {
  flex-basis: 100%;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .section-restaurants__list .restaurant-card {
    flex-basis: calc(50% - 0.5rem);
  }
  .section-restaurants__list .restaurant-card:nth-child(odd) {
    margin-right: 1rem;
  }
}

.section-rst-detail {
  width: 100%;
  height: 230px;
}
.section-rst-detail__img {
  height: 100%;
}
.section-rst-detail__img img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center center;
}
@media (min-width: 768px) {
  .section-rst-detail {
    height: 280px;
  }
  .section-rst-detail__img img {
    object-position: center 70%;
  }
}
@media (min-width: 1200px) {
  .section-rst-detail {
    height: 300px;
  }
  .section-rst-detail__img img {
    object-position: center 60%;
  }
}

.section-rst-menu {
  position: relative;
  z-index: 1;
  background-color: #F4F4F4;
  width: calc(100%);
  border-radius: 25px;
  margin-top: -2rem;
}
.section-rst-menu .whishlist {
  position: absolute;
  width: 70px;
  height: 60px;
  right: 0;
  top: 0.5rem;
}
@media (min-width: 768px) {
  .section-rst-menu .whishlist {
    width: 100px;
  }
}
@media (min-width: 1200px) {
  .section-rst-menu .whishlist {
    width: 150px;
    top: 1.5rem;
  }
}
.section-rst-menu .dishes {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
.section-rst-menu .dishes .dishes-list {
  padding: 1rem 1rem;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  flex-basis: 100%;
}
@media (min-width: 768px) {
  .section-rst-menu .dishes .dishes-list {
    flex-basis: calc(50% - 2rem);
  }
}
@media (min-width: 1200px) {
  .section-rst-menu .dishes .dishes-list {
    flex-basis: calc(33.33333% - 2rem);
  }
  .section-rst-menu .dishes .dishes-list:nth-child(4) {
    flex-basis: 100%;
    padding: 4rem 0;
  }
}
.section-rst-menu .dishes .dishes-list .menu-card {
  flex-basis: 100%;
  margin: 0.5rem 0;
}
.section-rst-menu .dishes .dishes-list__order {
  flex-basis: 100%;
  text-align: center;
  align-self: center;
}

.footer {
  background-color: #353535;
  color: #FFF;
  padding: 2rem 1.5rem;
}
.footer__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.footer__list li {
  flex-basis: 100%;
}
.footer__list .footer__link {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 0.1rem 0;
}
@media (min-width: 768px) {
  .footer__list .footer__link {
    justify-content: center;
  }
}
.footer__list .footer__link:hover {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1.1rem;
}
.footer__list .footer__link--highlighted {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1.1rem;
}
.footer__list .footer__link--highlighted:hover {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 1.1rem;
}
.footer__list .footer__link .icon {
  flex-basis: 2rem;
}
.footer__list .footer__link .icon.fa-rotate {
  transform: rotate(145deg);
  transform-origin: 20% 40%;
}
.footer__logo {
  width: 100px;
  padding-bottom: 1rem;
}
.footer__logo .logo--white {
  fill: #FFF;
}
@media (min-width: 768px) {
  .footer__logo {
    width: 150px;
    display: block;
    margin: auto;
  }
}

.btn-cta {
  display: inline-block;
  padding: 1rem 1.5rem;
  background-image: linear-gradient(#FF79DA, #9356DC);
  color: #FFF;
  border-radius: 25rem;
  text-decoration: none;
  box-shadow: 3px 3px 8px #D3D3D3;
  opacity: 1;
  transition: all 0.3s ease 0s;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1rem;
}
.btn-cta--large {
  padding: 1rem 4rem;
}
.btn-cta:hover {
  opacity: 0.8;
  box-shadow: 3px 3px 8px #666;
}

.whishlist__heart {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 25px;
}

.section-tutorial__card {
  border-radius: 15px;
  margin: 0.6rem 0;
}
.section-tutorial__card.card--styled {
  background-color: #F6F6F6;
  box-shadow: 3px 3px 8px #D3D3D3;
  padding: 1.2rem 0;
  margin-left: 0.5rem;
  line-height: 1.6rem;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 0.9rem;
}
@media (min-width: 768px) {
  .section-tutorial__card.card--styled {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: 1rem;
  }
}
.section-tutorial__card.card--styled .card__tile:nth-child(1) {
  background-color: #9356DC;
  color: #FFF;
  border-radius: 50px;
  width: 25px;
  height: 25px;
  text-align: center;
  align-content: center;
  margin-left: -0.5rem;
}
.section-tutorial__card.card--styled .card__tile:nth-child(2) {
  width: 55px;
  text-align: center;
  align-content: center;
}
.section-tutorial__card.card--styled i {
  color: #7E7E7E;
}
.section-tutorial__card.card--active {
  background-color: #F5EDFF;
}
.section-tutorial__card.card--active i {
  color: #9356DC;
}

.restaurant-card {
  position: relative;
  border-radius: 20px;
  background-color: #FFF;
  box-shadow: 3px 3px 8px #D3D3D3;
  line-height: 1.6rem;
}
.restaurant-card:nth-child(1) .card-inner__img {
  object-position: 0 50%;
}
.restaurant-card:nth-child(2) .card-inner__img {
  object-position: 0 62%;
}
.restaurant-card:nth-child(3) .card-inner__img {
  object-position: 0 100%;
}
.restaurant-card:nth-child(4) .card-inner__img {
  object-position: 0 100%;
}
.restaurant-card.tag::after {
  content: "Nouveau";
  position: absolute;
  top: 1rem;
  right: 1rem;
  background-color: #99E2D0;
  color: #278870;
  padding: 0.3rem 0.8rem;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 0.9rem;
}
@media (min-width: 768px) {
  .restaurant-card.tag::after {
    padding: 0.4rem 1rem;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: 1rem;
  }
}
.restaurant-card .card-inner {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.restaurant-card .card-inner__img {
  flex-basis: 100%;
  height: 175px;
  border-radius: 20px 20px 0 0;
  object-fit: cover;
}
@media (min-width: 1024px) {
  .restaurant-card .card-inner__img {
    height: 225px;
  }
}
@media (min-width: 1200px) {
  .restaurant-card .card-inner__img {
    height: 275px;
  }
}
.restaurant-card .card-inner__description {
  position: relative;
  flex-basis: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.restaurant-card .card-inner__description .title {
  flex-basis: 100%;
  margin: 1rem 0 0 1rem;
}
.restaurant-card .card-inner__description .whishlist {
  position: absolute;
  width: 80px;
  height: 60px;
  right: 0;
}
.restaurant-card .card-inner__description .district {
  flex-basis: 100%;
  margin: 0 0 1rem 1rem;
}

.section-rst-menu .menu-card--styled {
  background-color: #FFF;
  border-radius: 15px;
  box-shadow: 3px 3px 8px #D3D3D3;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.section-rst-menu .menu-card--styled .col-1 {
  flex-grow: 1;
  padding: 0.5rem 0 0.5rem 0.8rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.section-rst-menu .menu-card--styled .col-2 {
  flex-basis: 10%;
  padding: 0.5rem 0.8rem 0.5rem 0.2rem;
  text-align: right;
  align-self: flex-end;
}
.section-rst-menu .menu-card--styled .col-3 {
  display: none;
  position: relative;
  flex-basis: 20%;
  height: 100%;
  border-radius: 0 15px 15px 0;
  background-color: #99E2D0;
}
.section-rst-menu .menu-card--styled .col-3 i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #FFF;
}
.section-rst-menu .menu-card--selected .col-3 {
  display: block;
}